.hamburger {
  display: block;
  padding: 0 0.3125rem;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: 2rem;
  height: inherit;
  cursor: pointer;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 2rem;
  height: 0.0625rem;
  background-color: #fff;
  border-radius: 0.25rem;
  transition: 0.5s;
}

.hamburger span:nth-of-type(1) {
  top: 1.75rem;
}

.hamburger span:nth-of-type(2) {
  top: 2.5rem;
}

.hamburger span:nth-of-type(3) {
  top: 3.25rem;
}

.hamburger.is-open span:nth-of-type(1) {
  top: 2.3125rem;
  transform: translateX(-50%) rotate(45deg);
}

.hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.hamburger.is-open span:nth-of-type(3) {
  top: 2.1875rem;
  transform: translateX(-50%) rotate(-45deg);
}

